// component A styling

.component_a {
  display: flex;
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  button {
    padding: 0.5rem 1rem;
    border-radius: 0.4rem;
    font-weight: 600;
    color: white;
  }
}

// component B styling
.content_wrapper {
  width: 300px;
  height: 300px;
  background-color: #ececec;
  margin: 0 auto;
  border-radius: 0.4rem;
  display: flex;
  flex-direction: column;
  justify-content: center;

  .content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
    background-color: #fcc200;
    width: 250px;
    height: 250px;
    border-radius: 50%;

    h1 {
      font-size: 75px;
    }

    button {
      text-decoration: underline;
      font-size: 15px;
      font-weight: 600;
      color: black;
    }
  }
}
